import React from 'react';
import Form from '../common/form';
import payoutService from '../../services/payoutService';

 
class UserInformation extends Form {
    provinceInit = {
        idProvince:"None",
        name:"Chn Tnh/Thnh Ph ..."
    }

    districtInit = {
        idProvince:"None",
        idDistrict:"None",
        name:"Chn Qun/Huyn..."
    }

    communeInit = {
        idDistrict: "None",
        idCommune: "None",
        name: "Chn Phng/X..."
    }

    state = { 
        data : {},
        disabled: true,
        errors: {},
        serverError: {},
        province: [{...this.provinceInit}],
        district: [{...this.districtInit}],
        commune : [{...this.communeInit}],
        provinceValue: "None",
        districtValue : "None",
        communeValue : "None"
    }

    async componentDidMount() {
        let data = {...this.state.data};
        const provinceList = await payoutService.getProvince();
        const province =[{...this.provinceInit},...provinceList];
        this.setState({ data, province })
    }

    hanldeDistrict = async idProvince => {
        if (idProvince === "None"){
            this.setState( { district: [{...this.districtInit}], commune: [{...this.communeInit}]} )
        }
        else {
            const districtList  = await payoutService.getDistrict(idProvince);
            const district = [{...this.districtInit}, ...districtList];
            this.setState({ district, commune: [{...this.communeInit}] })
        }
    }

    hanldeCommune = async idDistrict => {
        if (idDistrict === "None"){
            this.setState( { commune: [{...this.communeInit}]} )
        }
        else {
            const communeList  = await payoutService.getCommune(idDistrict);
            const commune = [{...this.communeInit},...communeList];
            this.setState({ commune })
        }
    }

    render() { 
        const { province, district, commune} = this.state ;
        const { paymentMethod } = this.state.data;   
        const cardType = [
            {
                check:true,
                cardType: "None",
                name: "Chn Loi Th ..."
            },
            {
                cardType: "Visa",
                name: "Visa"
            },
            {
                cardType: "Master Card",
                name: "Master Card"
            },
            {
                cardType: "JCB",
                name: "JCB"
            }
        ]
        return ( <div className="user-information">
            <form className="form-account" onSubmit={this.handleSubmit}>
                <div className="personal-information">
                    {this.renderInputType3("userName","Tn ngi nhn", "Nhp h tn ngi nhn")}
                    {this.renderInputType3("userPhone","in thoi", "S in thoi gm 10 ch s","true")}
                    {this.renderSelect("userProvince","Tnh/Thnh Ph",province, "idProvince")}
                    {this.renderSelect("userDistrict","Qun/Huyn",district, "idDistrict")}
                    {this.renderSelect("userCommune","Phng/X",commune, "idCommune")}
                    {this.renderInputType3("userStreet","a Ch", "Nhp s nh, tn ng")}
                </div>
                <div className="personal-payment-method">
                {this.renderSelect("cardType","Loi th",cardType, "cardType")}
                    {this.renderInputType3("cardNumber","S Th", "S th gm 12 ch s...","true",12)}
                    {this.renderInputType3("cardOwner","Tn Ch Th", "Lu  nhp khp vi tn trn th")}
                    {this.renderInputType3("cardExpireDate","Ngy Ht Hn", "MM/YYYY")}
                    {this.renderInputType3("cardCvv","M CVV/CVC", "Gm 3 ch s in  mt sau ca th...","true",3)}
                </div>
            </form>
        </div> );
    }
}
 
export default UserInformation;

