import React from 'react';
import Form from '../components/common/form';
import './Payout.scss';
import addfunc from '../ultis/additionalFunction';
import payoutService from '../services/payoutService';
import { Link } from 'react-router-dom';
import sendOrder from '../services/orderService';
import additionalFunctionDom from '../ultis/additionalFunctionDom';


class Payout extends Form {
    provinceInit = {
        idProvince:"None",
        name:"Chn Tnh/Thnh Ph ..."
    }

    districtInit = {
        idProvince:"None",
        idDistrict:"None",
        name:"Chn Qun/Huyn..."
    }

    communeInit = {
        idDistrict: "None",
        idCommune: "None",
        name: "Chn Phng/X..."
    }

    state = { 
        data : {},
        errors: {},
        serverError: {},
        disabled: true,
        province: [{...this.provinceInit}],
        district: [{...this.districtInit}],
        commune : [{...this.communeInit}],
        provinceValue: "None",
        districtValue : "None",
        communeValue : "None"
    }

    inputCheck = {
        receiverName : "emptyCheck",
        receiverPhone : "phoneCheck",
        receiverProvince : "selectEmptyCheck",
        receiverDistrict : "selectEmptyCheck",
        receiverCommune : "selectEmptyCheck",
    }

    inputCheckForCardPayment = {
        cardType : "selectEmptyCheck",
        cardNumber : "phoneCheck",
        cardOwner : "emptyCheck",
        cardExpireDate : "checkCardExpireDate",
        cardCvv : "phoneCheck"
    }

    async componentDidMount() {
        window.scrollTo(0, 0);
        this.props.onLoadingScreen();
        additionalFunctionDom.fixBody();
        let data = {...this.state.data};
        data["paymentMethod"] = "cash";
        const provinceList = await payoutService.getProvince();
        const province =[{...this.provinceInit},...provinceList];
        this.setState({ data, province })
        document.title = "Thanh Ton";
        setTimeout( () => {
            this.props.onLoadingScreen();
            additionalFunctionDom.releaseBody();
        },1200)  
    }

    hanldeDistrict = async idProvince => {
        if (idProvince === "None"){
            this.setState( { district: [{...this.districtInit}], commune: [{...this.communeInit}]} )
        }
        else {
            const districtList  = await payoutService.getDistrict(idProvince);
            const district = [{...this.districtInit}, ...districtList];
            this.setState({ district, commune: [{...this.communeInit}] })
        }
    }

    hanldeCommune = async idDistrict => {
        if (idDistrict === "None"){
            this.setState( { commune: [{...this.communeInit}]} )
        }
        else {
            const communeList  = await payoutService.getCommune(idDistrict);
            const commune = [{...this.communeInit},...communeList];
            this.setState({ commune })
        }
    }

    doSubmit = async () => {
        const {data} = this.state;
        const { shoppingCart } = this.props;
        const oderInfo = new addfunc.GetPaymentInfo( data, shoppingCart)
        await sendOrder(oderInfo);
    }

    render() { 
        const { province, district, commune} = this.state ;
        let { shoppingCart } = this.props;
        shoppingCart = shoppingCart ? shoppingCart : [];
        const { paymentMethod } = this.state.data;   
        const cardType = [
            {
                check:true,
                cardType: "None",
                name: "Chn Loi Th ..."
            },
            {
                cardType: "Visa",
                name: "Visa"
            },
            {
                cardType: "Master Card",
                name: "Master Card"
            },
            {
                cardType: "JCB",
                name: "JCB"
            }
        ]
        return (  
            <main className="payout-main">
                <h2 className="payout-title">Thanh Ton</h2>
                <div className="payout-container d-flex justify-content-between">
                    <section className="payout-user-info">
                        <form onSubmit={this.handleSubmit} className="payout-form">
                            <div className="payout-user-info-detail">
                                <h3>Thng Tin Ngi Nhn</h3>
                                {this.renderInputType3("receiverName","Tn ngi nhn", "Nhp h tn ngi nhn")}
                                {this.renderInputType3("receiverPhone","in thoi", "S in thoi gm 10 ch s","true")}
                                {this.renderSelect("receiverProvince","Tnh/Thnh Ph",province, "idProvince")}
                                {this.renderSelect("receiverDistrict","Qun/Huyn",district, "idDistrict")}
                                {this.renderSelect("receiverCommune","Phng/X",commune, "idCommune")}
                                {this.renderInputType3("receiverStreet","a Ch", "Nhp s nh, tn ng")}
                                {this.renderTextArea("receiverNote","Ghi Ch", "Bn cn chng ti ch  iu g",5)}
                                <p className="empty-warning"><span className="obligation-mark">*</span> Bn khng c  trng mc ny</p>
                            </div>
                            <div className="payment-method">
                                <h3>Thng Tin Thanh Ton</h3>
                                {this.renderRadioInput("paymentMethod","cash-method","Thanh ton bng tin mt khi nhn hng","cash","true")}  
                                {this.renderRadioInput("paymentMethod","card-method","Thanh ton bng th thanh ton quc t","card")}  
                                <div className={paymentMethod === "card" ? "card-method-content displaying" : "card-method-content"}>
                                    {this.renderSelect("cardType","Loi th",cardType, "cardType")}
                                    {this.renderInputType3("cardNumber","S Th", "S th gm 12 ch s...","true",12)}
                                    {this.renderInputType3("cardOwner","Tn Ch Th", "Lu  nhp khp vi tn trn th")}
                                    {this.renderInputType3("cardExpireDate","Ngy Ht Hn", "MM/YYYY")}
                                    {this.renderInputType3("cardCvv","M CVV/CVC", "Gm 3 ch s in  mt sau ca th...","true",3)}
                                    <p className="empty-warning"><span className="obligation-mark">*</span> Bn khng c  trng mc ny</p>
                                </div>
                            </div>
                            <button className="order-button" >t Hng</button>
                            <p className="note-order-button">(Kim tra k thng tin trc khi nhn nt)</p>
                        </form>
                    </section>
                    <section className="payout-shopping-cart">
                        <div className="payout-shopping-container">
                            <div className="payout-shopping-header d-flex justify-content-between">
                                <h3 className="payout-shopping-title">n Hng</h3>
                                <Link to="/gio-hang"><div className="back-to-cart-button">Sa</div></Link>
                            </div>
                        </div>
                        <div className="payout-shopping-body">
                            {shoppingCart && shoppingCart.map( item =>
                                <div className="item-list" key={item.id}>
                                    <p>{item.count} x {item.name}</p>
                                    <p>= {addfunc.separator1000(item.count*item.price.replace(/\D/g,""))} VND</p>
                                </div>
                            )}
                        </div>
                        <div className="payout-shopping-footer">
                            <div className="provisional-sum d-flex justify-content-between"><span>Tm tnh:</span> {addfunc.separator1000(addfunc.totalMoneyCalculation(shoppingCart))} VND</div>
                            <div className="tax-vat d-flex justify-content-between"><span>Thu VAT: </span>{addfunc.separator1000(addfunc.totalMoneyCalculation(shoppingCart)/10)} VND</div>
                            <div className="total-money d-flex justify-content-between"><strong>Tng tin:</strong> <span> {addfunc.separator1000((addfunc.totalMoneyCalculation(shoppingCart)*1.1).toFixed(0))} VND</span></div>
                        </div>
                    </section>
                </div>
            </main>
          );
    }
}

export default Payout;