import React, {useEffect} from 'react';
import BreadCrumb from './common/breadCrumb';
import './ContactUs.scss';
import additionalFunctionDom from '../ultis/additionalFunctionDom';

const ContactUs = ({onLoadingScreen}) => {
    useEffect(() => {
        document.title = "Lin H";
        window.scrollTo(0, 0);
        onLoadingScreen();
        additionalFunctionDom.fixBody();
        setTimeout( () => {
            onLoadingScreen();
            additionalFunctionDom.releaseBody();
        },1200) 
    },[])

    return ( 
        <main className="contact-us">
            <BreadCrumb titleParent="Lin H"/>
            <section className="about-us">
                <h2 className="about-us-title title-section">Cu Chuyn V Chng Ti</h2>
                <p>u nhng nm 90 ca th k trc, khi m Rock v Metal bt u c ph bin rng ri hn ti Vit Nam, c bit l trong gii sinh vin. Khi y nim vui c s hu mt bng catset</p>
                <p>QTV Music c xy dng bi mt nhm cc bn tr am m vi nhc Rock/Metal u nhng nm 2010, vi mong mun lan ta v chia s nim am m c s hu nhng album cht lng n vi cc fan hm m ca th loi Rock/Metal ti Vit Nam.</p>
                <p>Tri qua gn 10 nm thnh lp, n nay QTV Music l nh phn phi bng a nhc online hng u ti Vit Nam, cc sn phm c gii thiu trn QTV Music u c cam kt m bo 100% l hng chnh hng c cc hng a pht hnh.</p>
            </section>
            <section className="shop-information ">
                <h2 className="shop-information-title title-section"> Thng Tin Lin H</h2>
                <div className="shop-information-container d-flex justify-content-between">
                    <div className="shop-google-map">
                        <iframe src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d3725.352280371583!2d105.7827840507617!3d20.978511294798526!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x3135acd2875155ef%3A0xb4e3002e25c0426b!2zSOG7kyBHxrDGoW0gUGxhemEgSMOgIMSQw7RuZw!5e0!3m2!1sen!2s!4v1596277513831!5m2!1sen!2s"></iframe>
                    </div>
                    <div className="shop-information-detail">
                        <h3>QTV Music Entertainment</h3>
                        <p>Tr s cng ty: Tng 10, ta nh H Gm Plaza, H ng, H Ni</p>
                        <p>S in thoi: <a href="tel:19001570">19001570</a> </p>
                        <p>Email: qtv-music@gmail.com</p>
                        <p>Website: qtv-music@gmail.com</p>
                    </div>
                </div>
            </section>
        </main>
     );
}
 
export default ContactUs;