import React, { Component } from 'react';
import Form from './common/form';
import './Register.scss';
import { Link } from 'react-router-dom';
import { toast } from 'react-toastify';
import registerNewUser from '../services/registerService';



class Register extends Form {
    state = { 
        data :{},
        errors : {} ,
        serverError: "",
        disabled : true
    }

    inputCheck = {
        emailRegister: "emailCheck",
        phoneRegister : "phoneCheck",
        passwordRegister : "passwordCheck",
        passwordRegisterRetype : "checkRetype"
    }

    doSubmit = async () => {
        try {
            const { data : user } = this.state
            await registerNewUser(user);
            toast.info('ng K Thnh Cng', {
                position: "top-center",
                autoClose: 2000,
                hideProgressBar: true,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
                });
            this.props.history.replace('/dang-nhap');
        }
        catch(ex){
            if (ex.response && ex.response.status === 400) {
                const serverError = "Email ny  c s dng"; 
                this.setState({ serverError })
            }
        }
    }

    handleToggle = (e) =>{
        e.preventDefault();
        this.props.history.replace('/dang-nhap')
    }

    render() { 
        return ( 
        <main className="register-section">
            <section className="register-container">
                <h2>NG K</h2>
                {this.state.serverError && <div className="valid-feedback">{this.state.serverError}</div>}
                <form onSubmit={this.handleSubmit} className="register-form">
                    {this.renderInputType2("emailRegister","Email ng K")}
                    {this.renderInputType2("nameRegister","H Tn")}
                    {this.renderInputType2("phoneRegister","S in Thoi","text","true")}
                    {this.renderInputType2("passwordRegister","Mt Khu Cha t Nht 6 K T","password")}
                    {this.renderInputType2("passwordRegisterRetype","Nhp Li Mt Khu","password")}
                    <p>Khi bn Khi bn nhn NG K, bn  ng  vi nhng <span><Link to="/dieu-khoan">iu Khon</Link></span> v <span><Link to="/chinh-sach">Chnh Sch</Link></span>  ca QTV Guitar Shop.</p>
                    <button type="submit" disabled={this.state.disabled}>ng K</button>
                </form>
                <p>Bn  c ti khon? <span><Link to="/dang-nhap" onClick={this.handleToggle}>ng nhp</Link></span></p>
            </section>
        </main>
        )
    }
}

export default Register;