import React, { useEffect } from 'react';
import './Blog.scss';
import { Link } from 'react-router-dom';
import item1 from '../assets/blog/Chester-Bennington.jpg';
import item2 from '../assets/blog/finland.jpeg';
import item3 from '../assets/blog/hac-san-1.jpg';
import item4 from '../assets/blog/thrash-metal.jpg';
import calendar from '../assets/blog/calendar-1.png';
import pen from '../assets/blog/pen-1.png';
import BreadCrumb from './common/breadCrumb';
import additionalFunctionDom from '../ultis/additionalFunctionDom';

const Blog = ({onLoadingScreen}) => {
    const title_1 = "Chester Bennington mt cuc i  nh";
    const title_2 = " Th Phn Lan Trong Cuc ua Ginh Ngi V Th  Metal";
    const title_3 = "Ban Nhc Hc San Ra Mt Album Hn Trng Mu";
    const title_4 = "Nhng iu Th V V T Tr Thrash Metal";

    useEffect(() => {
        document.title = "Blog";
        window.scrollTo(0, 0);
        onLoadingScreen();
        additionalFunctionDom.fixBody();
        setTimeout( () => {
            onLoadingScreen();
            additionalFunctionDom.releaseBody();
        },1200)  
    },[])

    return ( 
        <main className="blog-section">
            <BreadCrumb titleParent="Blog"/>
            <section className="article-section">
                <div className="article-item d-flex justify-content-between flex-row-reverse">
                    <div className="article-item-photo">
                        <div className="article-item-photo-container">
                            <img src={item1} alt="chester-bennington"/>
                        </div>
                    </div>
                    <div className="article-description">
                        <h2 className="article-description-title">Chester Bennington - Mt Cuc i  Nh</h2>
                        <div className="article-description-info d-flex">
                            <div className="article-item-date d-flex justify-content-between align-items-center">
                                <img src={calendar} alt="lch"/>
                                <p>20 Thng 07, 2020</p>
                            </div>
                            <div className="article-item-author d-flex justify-content-between align-items-center">
                                <img src={pen} alt="tc gi"/>
                                <p>Admin</p>
                            </div>
                        </div>
                        <div className="article-description-summary">
                            <p>Ngy 20 thng 07 nm 2017 , Chester Bennington chm dt cuc i ca mnh bng cch treo c t t,   li s nui tic v au bun trong lng cc fan hm m ca ban nhc LinKin Park. Hy cng QTV Music im li nhng s kin tiu biu trong cuc i thnh cng nhng cng n cha rt nhiu gc khut ca ging ca Nu metal ny.</p>
                        </div>
                        <Link to={`/blog/${title_1.split(" ").join("-")}-1`}>
                        <div className="button-access-article d-flex justify-content-center align-items-center">c Tip</div>
                        </Link>               
                    </div>
                </div>
                <div className="article-item d-flex justify-content-between">
                    <div className="article-item-photo">
                        <div className="article-item-photo-container">
                            <img src={item2} alt="Metal"/>
                        </div>
                    </div>
                    <div className="article-description">
                        <h2 className="article-description-title"> Th Phn Lan Trong Cuc ua Ginh Ngi V Th  Metal</h2>
                        <div className="article-description-info d-flex">
                            <div className="article-item-date d-flex justify-content-between align-items-center">
                                <img src={calendar} alt="lch"/>
                                <p>02 Thng 07, 2020</p>
                            </div>
                            <div className="article-item-author d-flex justify-content-between align-items-center">
                                <img src={pen} alt="tc gi"/>
                                <p>Admin</p>
                            </div>
                        </div>
                        <div className="article-description-summary">
                            <p>Vi s lng ban nhc chi heavy metal trn u ngi nhiu hn bt c ni no khc trn th gii, Phn Lan ang nm ti tm im ca cng ng metal trn khp ton cu. Chng ta cng xem cch ngi ta xc nh thnh ph no ca Phn Lan t danh hiu th  Metal.</p>
                        </div>
                        <Link to={`/blog/${title_2.split(" ").join("-")}-2`}>
                        <div className="button-access-article d-flex justify-content-center align-items-center">c Tip</div>
                        </Link>               
                    </div>
                </div>
                <div className="article-item d-flex justify-content-between flex-row-reverse">
                    <div className="article-item-photo">
                        <div className="article-item-photo-container">
                            <img src={item3} alt="Hac San"/>
                        </div>
                    </div>
                    <div className="article-description">
                        <h2 className="article-description-title">Ban Nhc Hc San ra mt album Hn  Trng  Mu</h2>
                        <div className="article-description-info d-flex">
                            <div className="article-item-date d-flex justify-content-between align-items-center">
                                <img src={calendar} alt="lch"/>
                                <p>25 Thng 06, 2020</p>
                            </div>
                            <div className="article-item-author d-flex justify-content-between align-items-center">
                                <img src={pen} alt="tc gi"/>
                                <p>Admin</p>
                            </div>
                        </div>
                        <div className="article-description-summary">
                            <p>Sau 5 nm k t album u tay St nh Ngang Tri, Hc San mi quay tr li cng album mi Hn - Trng - Mu, ly cm hng t cuc i thi s ni ting Hn Mc T, l ngi khi u cho dng th lng mn ca Vit Nam nhng nm u th k 20.</p>
                        </div>
                        <Link to={`/blog/${title_3.split(" ").join("-")}-3`}>
                        <div className="button-access-article d-flex justify-content-center align-items-center">c Tip</div>
                        </Link>               
                    </div>
                </div>
                <div className="article-item d-flex justify-content-between">
                    <div className="article-item-photo">
                        <div className="article-item-photo-container">
                            <img src={item4} alt="Thrash Metal"/>
                        </div>
                    </div>
                    <div className="article-description">
                        <h2 className="article-description-title">Nhng iu Th V V T Tr Thrash Metal</h2>
                        <div className="article-description-info d-flex">
                            <div className="article-item-date d-flex justify-content-between align-items-center">
                                <img src={calendar} alt="lch"/>
                                <p>08 Thng 06, 2020</p>
                            </div>
                            <div className="article-item-author d-flex justify-content-between align-items-center">
                                <img src={pen} alt="tc gi"/>
                                <p>Admin</p>
                            </div>
                        </div>
                        <div className="article-description-summary">
                            <p>Chc hn mi ngi  qu quen thuc vi 4 ng tng trong lng Thrash Metal: Metallica, Slayer, Megadeth v Anthrax, h l nhng band i u trong dng nhc Thrash v  khng nh c v th ca mnh t nhng nm 80 cho n tn ngy hm nay.</p>
                        </div>
                        <Link to={`/blog/${title_4.split(" ").join("-")}-4`}>
                        <div className="button-access-article d-flex justify-content-center align-items-center">c Tip</div>
                        </Link>               
                    </div>
                </div>
            </section>
        </main>
     );
}
 
export default Blog;